@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap');

body {
  margin: 0;
  background: linear-gradient(135deg, #1a1a1a 0%, #2D2D2D 100%);
  color: white;
  overflow-x: hidden;
}

/* Floating McRib background */
.bg-floating-mcrib {
  background-image: url('data:image/svg+xml;utf8,<svg xmlns=\"http://www.w3.org/2000/svg\" viewBox=\"0 0 32 32\"><rect width=\"32\" height=\"32\" fill=\"%231a1a1a\"/><rect x=\"2\" y=\"4\" width=\"28\" height=\"10\" rx=\"5\" fill=\"%23DEB887\"/><rect x=\"4\" y=\"12\" width=\"24\" height=\"10\" rx=\"3\" fill=\"%238B4513\"/><rect x=\"2\" y=\"20\" width=\"28\" height=\"10\" rx=\"5\" fill=\"%23D2B48C\"/></svg>');
  background-size: 80px;
  opacity: 0.08;
  animation: float 20s infinite linear;
  pointer-events: none;
}

/*
 * Your existing mobile override — untouched.
 */
@media (max-width: 600px) {
  .App-header {
    font-size: calc(5px + 2vmin) !important;
  }
  img {
    max-width: 100% !important; /* THIS IS THE CRITICAL LINE THAT MUST WIN */
    height: auto !important;
  }
}

/* =====================================================
   MOBILE RESPONSIVE FIXES (APPENDED — NOTHING REMOVED)
   ===================================================== */

/* General mobile scaling for tablets and smaller screens */
@media (max-width: 900px) {
  body {
    font-size: 90%;
  }

  h1, h2, h3 {
    font-size: 90%;
  }

  .mint-section,
  .header,
  .footer,
  .stats-wrapper {
    padding: 10px !important;
  }

  img {
    max-width: 100% !important;
    height: auto !important;
  }

  button {
    font-size: 90% !important;
    padding: 10px 14px !important;
  }
}

/* Phone-specific adjustments */
@media (max-width: 600px) {
  body {
    font-size: 80%;
  }

  h1, h2, h3 {
    font-size: 85%;
  }

  .mint-section,
  .header,
  .footer,
  .stats-wrapper {
    padding: 6px !important;
  }

  button {
    font-size: 85% !important;
    padding: 8px 12px !important;
  }
}
